.formGroup {
    position: relative;
    padding: 15px 0 0;
    margin-top: 10px;
    width: 100%;
}

.formField {
    font-family: inherit;
    width: 100%;
    border: 0;
    border-bottom: 2px solid #00b5ff;
    outline: 0;
    font-size: 1.3rem;
    color: #00b5ff;
    padding: 7px 0;
    background: transparent;
    transition: border-color 0.2s;
}

.formField:focus {
    padding-bottom: 6px;
    border-width: 3px;
    border-image: linear-gradient(to right, #2a40c2, #00b5ff);
    border-image-slice: 1;
}

.formLabel {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: #00b5ff;
}